import React, {useState} from 'react';

const URL = "http://localhost:5000/createUser";

export const RegisterPage = props => {
    const [username, setUsername] = useState("");
    const [password, setPassword] = useState("");

    function handleUsername(e) { setUsername(e.target.value); }

    function handlePassword(e) { setPassword(e.target.value); }

    function handleSubmit(e) {
        console.log("submit");
        const user = {
            username: username,
            password: password
        }

        
        const request = new Request(URL, {
            method: 'post',
            body: JSON.stringify(user),
            mode: 'default',
            // headers: new Headers({
            //     'Content-Type': 'application/json',
            //     'Accept': 'application/json',
            // })
        });

        fetch(request)
            .then(res => res.json())
            .then(res => console.log(res))
    }

    return (
        <React.Fragment>
            <div className="col-lg-8 offset-lg-2">
                <h2>Register</h2>
                <form name="form" onSubmit={handleSubmit}>
                    <div className="form-group">
                        <label>Username</label>
                        <input type="text" name="username" value={username} placeholder="Enter Username" onChange={handleUsername} required />
                    </div>
                    <div className="form-group">
                        <label>Password</label>
                        <input type="password" name="password" value={password} placeholder="Enter Password" onChange={handlePassword} required />
                    </div>
                    <div className="form-group">
                        <button className="btn btn-primary" type='submit'>
                            Login
                        </button>
                        <div>REGISTER</div>
                    </div>
                </form>
            </div>

        </React.Fragment>
    );
}



