import React, {useState} from 'react';

import {LoginPage} from './components/login/LoginComponent';
import {RegisterPage} from './components/register/RegisterComponent';
import {HomePage} from './components/home/HomeComponent';

function App() {
    const [toggleLoginView, setToggleLoginView] = useState(true);
    const [toggleRegisterView, setToggleRegisterView] = useState(false);
    const [toggleHomePageView, setToggleHomePageView] = useState(false);

    const handleLoginView = () => {
        setToggleLoginView(!toggleLoginView);

        /* Untoggling the Film and Planet views if selected */
        if(toggleRegisterView)
            setToggleRegisterView(!toggleRegisterView);    
        if(toggleHomePageView)
            setToggleHomePageView(!toggleHomePageView);
    }
    
    const handleRegisterView = () => {
        setToggleRegisterView(!toggleRegisterView);

        /* Untoggling the Character and Planet views if selected */
        if(toggleLoginView)
            setToggleLoginView(!toggleLoginView);
        if(toggleHomePageView)
            setToggleHomePageView(!toggleHomePageView);
    }

    const handleHomePageView = () => {
        setToggleHomePageView(!toggleHomePageView);

        /* Untoggling the Character and Film views if selected */
        if(toggleLoginView)
            setToggleLoginView(!toggleLoginView);

        if(toggleRegisterView)
            setToggleRegisterView(!toggleRegisterView);
    }

    return (
        <div className="App">
      <header className="App-header">
        <div className="buttonBar">
          <button className="searchButtons" onClick={handleLoginView}>LogIn</button>
          <button className="searchButtons" onClick={handleRegisterView}>Register</button> 
          <button className="searchButtons" onClick={handleHomePageView}>HomePage</button> 
        </div>
        <br />
  
        <br />
        {toggleLoginView ? (
          <div>
            <LoginPage />
          </div>
        ) : toggleRegisterView ? (
          <div>
            <RegisterPage />
          </div>
        ) : toggleHomePageView ? (
          <div>
            <HomePage />
          </div>
        ) : (
          <div>Please Select A Tab</div>
        )}
        <br />
      </header>
    </div>
    )
}

export default App;